import { Component, OnInit } from '@angular/core';
import { FormGroup, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/users/user.service';

@Component({
  selector: 'app-barber-edit',
  templateUrl: './barber-edit.component.html',
  styleUrls: ['./barber-edit.component.scss']
})
export class BarberEditComponent implements OnInit {
  barbers: any[]= [];

  public ownerForm: FormGroup;
  roles: any[];
  partner: any;

  constructor(
    private userService: UserService,
    private router: Router,
  ) {
    this.partner = {}
  }

  ngOnInit(): void {
    this.getBarbers();
  }


  getBarbers() {
    this.userService.getBarbers()
    .subscribe(
      data => { this.barbers = data; }
    )
  }



  onSubmit(form: NgForm) {
    if (form.invalid) {
      return;
    } else {
      this.userService.createBarber(this.partner)
        .subscribe(
          data => {
            this.getBarbers();
            this.closePartner();
          },
          error => { console.log(error + ''); }
        );
    }
  }


  closePartner() {
    this.router.navigate(['/dashboard/barbers']);
  }
}


