import { Component, OnInit } from '@angular/core';
import { AppConfig } from 'src/app/app.config';
import { OperationService } from 'src/app/services/operations/operation.service';
import * as jwt_decode from 'jwt-decode';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/users/user.service';


@Component({
  selector: 'app-barbers',
  templateUrl: './barbers.component.html',
  styleUrls: ['./barbers.component.scss']
})
export class BarbersComponent implements OnInit {
  barbers: any[] = [];
  viewbarbers: any[] = [];
  searchBarber: string;
  constructor(
    private operationService: OperationService,
    private configuration: AppConfig,
    private userService: UserService,
    private router: Router
    // private alertService: ToasterSer
  ) { }

  ngOnInit() {
    this.getBarbers();
  }

  getBarbers() {
    this.userService.getBarbers()
    .subscribe(
      data => { this.barbers = data; }
    )
  }


  // serveClient(event) {
  //   this.operationService.serveClient(event.id)
  //     .subscribe(
  //       data => { this.loadReservations(); },
  //       error => { }
  //     )
  // }

  // filtering application
  filterSessions(firter) {
    console.log(firter);
    if (firter === '0') {
      this.viewbarbers = this.barbers;
    } else {
      this.viewbarbers = this.barbers.filter(session => {
        console.log(session.isServed.toString());
        return session.isServed.toString() === firter + '';
      });
    }
  }


  newBarber() {
    this.router.navigate(['dashboard/barber-edit']);
  }
}
